@use "../base/variables" as v;
@use "../base/mixins" as m;

.contenedor-app {
  @include m.tablet {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    min-height: 100vh;
  }
  .imagen {
    background-image: url(../../img/Dental-Clinic.jpg);
    background-size: cover;
    background-position: center center;
  }
}
