/** Media Queries **/
@media (min-width: 768px) {
  .contenedor-app {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    min-height: 100vh;
  }
}
.contenedor-app .imagen {
  background-image: url(../../img/Dental-Clinic.jpg);
  background-size: cover;
  background-position: center center;
}